<template>
  <div class="BookImmediately">
    <main>
      <div class="title">
        <h3>{{fomtInfo.categoryName}}</h3>
        <p>
          <span>入住: {{fomtInfo.roomInTime}}</span>
          <template v-if="fomtInfo.checkStandard!=2">
            <span>离店： {{fomtInfo.roomInTime}}</span>共1晚</template>
          <template v-if="fomtInfo.checkStandard==2">共{{roomInfo.checkInTime/60}}小时</template>
        </p>
      </div>
      <div class="weui-cells weui-cells_form mb20">
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">房间数</label>
          </div>
          <div class="weui-cell__bd">
            <InputNumber :num="fomtInfo.number" :minnum="minnum" :maxnum="maxnum" :changefn="changefn"></InputNumber>
          </div>
        </div>
        <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="Choicetimefn" v-if="fomtInfo.checkStandard==2">
          <div class="weui-cell__hd">
            入住时间
          </div>
          <div class="weui-cell__bd">
            <p v-if="!fomtInfo.hourRoomCheckInTime">请选择入住时间</p>
            {{fomtInfo.hourRoomCheckInTime}}
          </div>
        </div>
      </div>
      <div class="weui-cells weui-cells_form mb20">
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">姓名</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="fomtInfo.customerName" placeholder="请输入入住人姓名">
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">手机</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="fomtInfo.customerPhone" placeholder="请输入入住人手机号码">
          </div>
        </div>

        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">备注</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="fomtInfo.remark" placeholder="对本次交易的备注，可不填">
          </div>
        </div>
      </div>
      <div class="weui-cells weui-cells_form">
        <div class="weui-cell weui-cell_select weui-cell_select-after" v-if="this.vipInfo">
          <div class="weui-cell__hd">
            <label for="" class="weui-label">优惠券
              <div class="Prompt red" v-if="isActive>-1">{{Coupon[isActive].title}}抵￥{{PriceData.couponPrice|money}}元</div>
              <div class="Prompt" v-if="Coupon.length==0">无可用</div>
            </label>
          </div>
          <div class="weui-cell__bd">
          </div>
        </div>
        <div class="Coupons" v-if="Coupon.length!=0">>
          <div class="Coupon" v-for="(item,index) in Coupon" :class="{ active: isActive==index }" @click="switchfn(item,index)" :key="index">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-icon-test"></use>
            </svg>
            <span>{{item.title}}</span>
          </div>
        </div>
        <div class="weui-cell weui-cell_switch" v-if="this.vipInfo">
          <div class="weui-cell__bd">积分
            <div class="Prompt" v-if="!jifenswitch">无可用</div>
            <div class="Prompt red" v-if="jifenswitch">共{{vipInfo.integral}}积分，可抵￥{{vipInfo.jifenMoeny}}元</div>
          </div>
          <div class="weui-cell__ft">
            <label for="switchCP" class="weui-switch-cp">
              <input id="switchCP" class="weui-switch-cp__input" type="checkbox" v-model="useIntegral" :disabled='!jifenswitch'>
              <div class="weui-switch-cp__box"></div>
            </label>
          </div>
        </div>
        <div class="weui-cell weui-cell_switch" v-if="this.vipInfo">
          <div class="weui-cell__bd">粉币
            <div class="Prompt" v-if="!fenbiswitch">无可用</div>
            <div class="Prompt red" v-if="fenbiswitch">共{{vipInfo.fans_currency}}粉币，可抵￥{{vipInfo.fenbiMoeny}}元</div>
          </div>
          <div class="weui-cell__ft">
            <label for="switchCP1" class="weui-switch-cp">
              <input id="switchCP1" v-model="useCoin" class="weui-switch-cp__input" type="checkbox" :disabled='!fenbiswitch'>
              <div class="weui-switch-cp__box"></div>
            </label>
          </div>
        </div>
        <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="abcd">
          <div class="weui-cell__hd">
            支付方式
          </div>
          <div class="weui-cell__bd">
            <p v-if="!PaymentMethod">请选择支付方式</p>
            {{PaymentMethod}}
          </div>
        </div>
      </div>
      <div class="Settlement">
        <div class="weui-cells">
          <div class="weui-cell">
            <div class="weui-cell__bd">
              <p>房费：</p>
            </div>
            <div class="weui-cell__ft">￥{{PriceData.roomPrice|money}}</div>
          </div>
        </div>
        <div class="weui-cells">
          <div class="weui-cell">
            <div class="weui-cell__bd">
              <p>押金：</p>
            </div>
            <div class="weui-cell__ft">+￥{{PriceData.deposit|money}}</div>
          </div>
        </div>
        <div class="weui-cells" v-if="PriceData.couponPrice>0">
          <div class="weui-cell">
            <div class="weui-cell__bd">
              <p>优惠券：</p>
            </div>
            <div class="weui-cell__ft">-￥{{PriceData.couponPrice|money}}</div>
          </div>
        </div>

        <div class="weui-cells" v-if="PriceData.integralPrice>0">
          <div class="weui-cell">
            <div class="weui-cell__bd">
              <p>积分：</p>
            </div>
            <div class="weui-cell__ft">-￥{{PriceData.integralPrice|money}}</div>
          </div>
        </div>
        <div class="weui-cells" v-if="PriceData.fenbiPrice>0">
          <div class="weui-cell">
            <div class="weui-cell__bd">
              <p>粉币：</p>
            </div>
            <div class="weui-cell__ft">-￥{{PriceData.fenbiPrice|money}}</div>
          </div>
        </div>
        <div class="weui-cells">
          <div class="weui-cell">
            <div class="weui-cell__bd">
              <p>订单总额：</p>
            </div>
            <div class="weui-cell__ft">￥{{PriceData.payPrice|money}}</div>
          </div>
        </div>
      </div>
    </main>
    <section class="placeOrder">
      <a class="weui-btn weui-btn_primary fr" @click="Submit">
        提交订单
      </a>
      <div class="placeOrder-text">实付款：
        <span class="red">￥{{PriceData.payPrice|money}}</span>
      </div>
    </section>
  </div>
</template>

<script type='text/ecmascript-6'>
import { roomBooking } from "@/assets/js/request.js";
import { DateFormat, emptytest, phonetest } from "@/assets/js/filters.js";
import InputNumber from "@/components/InputNumber";
import weui from "@/assets/js/weui.min";
export default {
  data() {
    return {
      hotelId: this.$store.state.hotelId,
      categoryId: this.$route.params.categoryId,
      roomOutTime: "",
      roomInTime: "",
      roomInfo: "",
      vipInfo: "",
      fomtInfo: {
        hotelId: this.$store.state.hotelId,
        activityId: null,
        categoryId: this.$route.params.categoryId,
        hourRoomCheckInTime: null,
        categoryName: null,
        checkStandard: null,
        couponsDiscount: null,
        couponsCode: null,
        couponsNum: null,
        customerName: null,
        customerPhone: null,
        deposit: null,
        displayPrice: null,
        fb: null,
        fbDiscount: null,
        integral: null,
        integralDiscount: null,
        number: 1,
        payPrice: null,
        payType: null,
        rackRate: null,
        remark: null,
        roomInTime: null,
        roomOutTime: null
      },
      PriceData: {
        couponPrice: 0,
        deposit: 0,
        fenbiPrice: 0,
        integralPrice: 0,
        payPrice: 0,
        roomPrice: 0
      },
      jifenswitch: false,
      fenbiswitch: false,
      useIntegral: false,
      useCoin: false,
      value: "在线支付",
      abc: true,
      num: 1,
      minnum: 1,
      maxnum: null,
      Coupon: [],
      timecha: [],
      isActive: -1,
      PaymentMethod: "",
      Choicetime: "",
      paymentmode: []
    };
  },
  methods: {
    changefn(val) {
      this.fomtInfo.number = val;
      this.getPricefn();
    },
    switchfn(item, index) {
      if (this.isActive == index) {
        this.isActive = -1;
        this.fomtInfo.couponsCode = null;
        this.fomtInfo.couponsNum = null;
      } else {
        this.isActive = index;
        this.fomtInfo.couponsCode = item.couponsCode;
      }
      this.getPricefn();
    },
    abcd() {
      var _this = this;
      weui.picker(_this.paymentmode, {
        onConfirm: function(result) {
          _this.PaymentMethod = result[0].label;
          _this.fomtInfo.payType = result[0].value;
        }
      });
    },
    Choicetimefn() {
      var _this = this;
      weui.picker(_this.timecha, {
        onConfirm: function(result) {
          _this.fomtInfo.hourRoomCheckInTime = result[0].label;
        }
      });
    },
    getPricefn() {
      var _this = this;
      var newdata = JSON.parse(JSON.stringify(this.fomtInfo));
      if (this.fomtInfo.checkStandard == 2) {
        if (newdata.hourRoomCheckInTime) {
          newdata.roomInTime =
            newdata.roomOutTime + " " + newdata.hourRoomCheckInTime + ":00";
          newdata.hourRoomCheckInTime = newdata.hourRoomCheckInTime + ":00";
        } else {
          newdata.roomInTime =
            newdata.roomOutTime + " " + _this.roomInfo.checkInPeriod;
          newdata.hourRoomCheckInTime = _this.roomInfo.checkInPeriod;
        }
        newdata.roomInTime = newdata.roomInTime
          .replace("年", "-")
          .replace("月", "-")
          .replace("日", "");
        newdata.roomOutTime = DateFormat(
          new Date(newdata.roomInTime).getTime() +
            _this.roomInfo.checkInTime * 60 * 1000,
          "yyyy-MM-dd hh:mm:ss"
        );
      } else {
        newdata.roomInTime = newdata.roomInTime + " 14:00:00";
        newdata.roomInTime = newdata.roomInTime
          .replace("年", "-")
          .replace("月", "-")
          .replace("日", "");
        newdata.roomOutTime = newdata.roomOutTime + " 12:00:00";
        newdata.roomOutTime = newdata.roomOutTime
          .replace("年", "-")
          .replace("月", "-")
          .replace("日", "");
      }
      roomBooking.getPrice({
        Suffix: _this.hotelId + "/getPrice",
        params: newdata,
        fn: data => {
          _this.PriceData = data;
          _this.fomtInfo.couponsDiscount = data.couponPrice;
          _this.fomtInfo.fbDiscount = data.fenbiPrice;
          _this.fomtInfo.integralDiscount = data.integralPrice;
          _this.fomtInfo.payPrice = data.payPrice;
          this.fomtInfo.couponsNum = data.couponCount;
          _this.judgejifen();
          _this.judgefenbi();
          _this.judgecoupons();
        }
      });
    },
    //判断积分能不能用
    judgejifen() {
      if (
        this.vipInfo.jifenMoeny > 0 &&
        this.vipInfo.jifenMoeny / this.vipInfo.jifenRatio <
          this.vipInfo.integral
      ) {
        if (this.vipInfo.getJifenMoeny * 100 <= this.PriceData.roomPrice) {
          this.jifenswitch = true;
        }
      }
    },
    //判断粉币能不能用
    judgefenbi() {
      if (
        this.vipInfo.fenbiMoeny > 0 &&
        this.vipInfo.fenbiMoeny / this.vipInfo.fenbiRatio <
          this.vipInfo.fans_currency
      ) {
        if (this.vipInfo.getFenbiMoeny * 100 <= this.PriceData.roomPrice) {
          this.fenbiswitch = true;
        }
      }
    },
    //判断那些优惠券可以用
    judgecoupons() {
      var _this = this;
      if (this.vipInfo.duofenCards) {
        this.vipInfo.duofenCards.forEach(item => {
          _this.Coupon = [];
          // 0折扣券 1减免券
          if (item.card_type == 1) {
            if (item.cash_least_cost * 1000 <= this.PriceData.roomPrice) {
              _this.Coupon.push({
                title: item.reduce_cost + "元" + item.title,
                couponsCode: item.code,
                couponsNum: item.couponsNum
              });
            }
          } else if (item.card_type == 0) {
            _this.Coupon.push({
              title: item.discount + "折" + item.title,
              couponsCode: item.code,
              couponsNum: item.couponsNum
            });
          }
        });
      }
    },
    Submit() {
      if (this.fomtInfo.checkStandard == 2) {
        if (!this.fomtInfo.hourRoomCheckInTime) {
          this.Toast("请选择入住时间");
          return false;
        }
      }
      if (!emptytest(this.fomtInfo.customerName)) {
        this.Toast("请输入正确的姓名");
        return false;
      }
      if (!phonetest(this.fomtInfo.customerPhone)) {
        this.Toast("请输入正确的电话号码");
        return false;
      }
      if (!this.PaymentMethod) {
        this.Toast("请选择支付方式");
        return false;
      }
      var _this = this;
      var newdata = JSON.parse(JSON.stringify(this.fomtInfo));
      if (this.fomtInfo.checkStandard == 2) {
        if (newdata.hourRoomCheckInTime) {
          newdata.roomInTime =
            newdata.roomOutTime + " " + newdata.hourRoomCheckInTime + ":00";
          newdata.hourRoomCheckInTime = newdata.hourRoomCheckInTime + ":00";
        }
        newdata.roomInTime = newdata.roomInTime
          .replace("年", "-")
          .replace("月", "-")
          .replace("日", "");
        newdata.roomOutTime = DateFormat(
          new Date(newdata.roomInTime).getTime() +
            _this.roomInfo.checkInTime * 60 * 1000,
          "yyyy-MM-dd hh:mm:ss"
        );
      } else {
        newdata.roomInTime = newdata.roomInTime + " 14:00:00";
        newdata.roomInTime = newdata.roomInTime
          .replace("年", "-")
          .replace("月", "-")
          .replace("日", "");
        newdata.roomOutTime = newdata.roomOutTime + " 12:00:00";
        newdata.roomOutTime = newdata.roomOutTime
          .replace("年", "-")
          .replace("月", "-")
          .replace("日", "");
      }
      roomBooking.Submitobj({
        Suffix: _this.hotelId + "/submit",
        params: newdata,
        fn: data => {
          this.$router.push("/payment/" + data.orderId);
        }
      });
    }
  },
  components: {
    InputNumber
  },
  mounted() {
    this.$store.dispatch("getHotelInfo").then(() => {
      //判断是否微信登陆
      function isWeiXin() {
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == "micromessenger") {
          return true;
        } else {
          return false;
        }
      }
      //在线支付
      if (this.$store.state.HotelInfo.payMode == 1) {
        if (isWeiXin()) {
          this.paymentmode = [
            {
              label: "微信",
              value: 1
            }
          ];
        } else {
          this.paymentmode = [
            {
              label: "支付宝",
              value: 0
            },
            {
              label: "微信",
              value: 1
            }
          ];
        }
      } else if (this.$store.state.HotelInfo.payMode == 2) {
        this.paymentmode = [
          {
            label: "到店支付",
            value: 2
          }
        ];
      } else if (this.$store.state.HotelInfo.payMode == 3) {
        console.log(isWeiXin(), "333");
        if (isWeiXin()) {
          this.paymentmode = [
            {
              label: "微信",
              value: 1
            },
            {
              label: "到店支付",
              value: 2
            }
          ];
        } else {
          this.paymentmode = [
            {
              label: "支付宝",
              value: 0
            },
            {
              label: "微信",
              value: 1
            },
            {
              label: "到店支付",
              value: 2
            }
          ];
        }
      }
      //获取vip信息
      roomBooking.getRoomInfo({
        Suffix: this.hotelId + "/book/" + this.categoryId,
        fn: data => {
          var _this = this;
          this.vipInfo = data;
          this.fomtInfo.customerName = data.nickName;
          this.fomtInfo.customerPhone = data.phone;
          if (this.vipInfo) {
            this.paymentmode.push({
              label: "会员卡支付",
              value: 3
            });
          }
        }
      });

      this.getPricefn();
    });
    this.fomtInfo.roomOutTime = sessionStorage.getItem("storeOutTime");
    this.fomtInfo.roomInTime = sessionStorage.getItem("storeInTime");
    this.roomInfo = JSON.parse(sessionStorage.getItem("storeRoom"));
    if (this.roomInfo.name) {
      this.fomtInfo.categoryName = this.roomInfo.name;
    } else {
      this.fomtInfo.categoryName = this.roomInfo.categoryName;
    }
    //判断是不是活动房
    if (this.roomInfo.activityType) {
      this.fomtInfo.checkStandard = this.roomInfo.activityType;
      this.fomtInfo.activityId = this.roomInfo.activityId;
      //如果是团购房有最低房间数
      if (this.roomInfo.activityType == 4) {
        this.fomtInfo.number = this.roomInfo.minPurchaseNumber;
        this.minnum = this.roomInfo.minPurchaseNumber;
      }
    } else {
      this.fomtInfo.checkStandard = 0;
      this.fomtInfo.activityId = null;
    }
    //判断能买多少间  checkStandard==0 代表不是活动房间 不是活动房间没限购 只要不大于剩下房间就可以
    if (this.fomtInfo.checkStandard == 0) {
      if (this.roomInfo.spare) {
        this.maxnum = this.roomInfo.spare;
      }
    } else {
      //restrictionEnable 活动房间的字段  0表示开启限购  restrictionNumber 限购数量
      if (
        this.roomInfo.restrictionEnable == 0 &&
        this.roomInfo.remainder >= this.roomInfo.restrictionNumber
      ) {
        this.maxnum = this.roomInfo.restrictionNumber;
      } else {
        this.maxnum = this.roomInfo.remainder; //活动房间有remainder字段  代表剩下房间
      }
    }
    if (this.fomtInfo.checkStandard == 2) {
      var timestart =
        this.roomInfo.checkInPeriod.split(":")[0] * 60 -
        0 +
        (this.roomInfo.checkInPeriod.split(":")[1] - 0);

      var timeend =
        this.roomInfo.checkOutPeriod.split(":")[0] * 60 -
        0 +
        (this.roomInfo.checkOutPeriod.split(":")[1] - 0);
      var cha = timeend - timestart + 1;
      var newarr = [];
      for (let i = 0; i < cha; i++) {
        var shi = Math.floor(timestart / 60);
        var fen = timestart % 60;
        if (shi < 10) {
          shi = "0" + shi;
        }
        if (fen < 10) {
          fen = "0" + fen;
        }
        newarr.push({
          label: shi + ":" + fen
        });
        timestart++;
      }
      this.timecha = newarr;
    }
    this.fomtInfo.deposit = this.roomInfo.deposit;
    if (this.fomtInfo.checkStandard == 0) {
      this.fomtInfo.displayPrice = this.roomInfo.displayPrice;
    } else {
      this.fomtInfo.displayPrice = this.roomInfo.activityPrice;
    }
    this.fomtInfo.rackRate = this.roomInfo.rackRate;

    this.getPricefn();
  },
  watch: {
    useIntegral: function(val, oldVal) {
      //开启
      if (this.useIntegral) {
        this.fomtInfo.integral =
          (this.vipInfo.jifenMoeny / this.vipInfo.jifenRatio).toFixed(2) * 100;
        this.fomtInfo.integralDiscount = this.vipInfo.jifenMoeny;
      } else {
        this.fomtInfo.integralDiscount = null;
        this.fomtInfo.integral = null;
      }
      this.getPricefn();
    },
    //粉币
    useCoin: function(val, oldVal) {
      //开启
      if (this.useCoin) {
        this.fomtInfo.fb =
          (this.vipInfo.fenbiMoeny / this.vipInfo.fenbiRatio).toFixed(2) * 100;
        this.fomtInfo.fbDiscount = this.vipInfo.fenbiMoeny;
      } else {
        this.fomtInfo.fb = null;
        this.fomtInfo.fbDiscount = null;
      }
      this.getPricefn();
    }
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
.BookImmediately {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

main {
  flex: 1;
  background: #f4f4f4;
}
</style>



